.titolo {
  font-family: ui-sans-serif;
  margin-inline: auto;
  justify-content: center;
  display: flex;
  background: -webkit-linear-gradient(	#6495ed, darkblue);
  -webkit-background-clip: text;
  color: transparent; 
  background-clip: text;
  width: auto;
}
P {
  background: -webkit-linear-gradient(	#6495ed, darkblue);
  -webkit-background-clip: text;
  color: transparent; /* Rende il testo trasparente, mostrando solo il gradiente */
}
.custom-component {
    text-align: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  .allCustomContainer {
    background: fixed;
    background-position: center;
    background-image: url('../../Assets/background3.jpg');
    background-size: cover;
    background-repeat: no-repeat;
  }
  .customButton {
    background: "linear-gradient(to right, #D3D3D3, #A9A9A9)";
    color: #1045a5;
    border: none;
    font-weight: bold;
    margin: 1em auto 5em auto;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  .image-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-top: 4em;
  }
  .shrink-image img {
  width: 15em;
  }
  .image-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .image-item img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
  }
  @media (max-width: 330px) {
   
    .titolo {  
      text-align: center;
    }
  }
  @media (max-width: 768px) {
    .image-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 530px) {
    .image-grid {
      grid-template-columns: 1fr;
    }
    .carosello {    
      flex-direction: column;
    }
    .contenitoreCarosello {
      flex-direction: column-reverse;
    }
    .carosello img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
  @media (max-width: 830px) {
   
    .carosello {  
        align-items: center;
    }
    .contenitoreCarosello {
flex-direction: column;
    }
    .carosello img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
  hr {
    width: 80%;
  }
  .contenitoreCarosello {
    display: flex;
  }
  /* carousel */
  .carosello {
     /* width: 20em; */
    position: relative;
    max-width: 100%;
    margin: auto;
    overflow: hidden;
    display: flex;
    margin-inline: auto;
    justify-content: center;
  }
  
  .carosello img {
    width: 15em;
    height: auto; /* Mantiene il rapporto d'aspetto */
    display: block;
  }
  .pstyle {
    display: flex;
    margin-inline: auto;
    justify-content: space-evenly;
  }
  