
.contenitoreChiSiamo {
    background-image: url('../../Assets/background3.jpg');
    background-size: cover;
    background-position: center;
}
.titoloChiSiamo {
    font-family: ui-sans-serif;
    background: -webkit-linear-gradient(	#4969a3, darkblue);
    -webkit-background-clip: text;
    color: transparent; /* Rende il testo trasparente, mostrando solo il gradiente */
}
.chi-siamo-background {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.card {
    background: white;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    max-width: 800px; /* Puoi regolare questa larghezza */
    width: 100%;
}

.chi-siamo-container {
    padding: 20px;
    font-family: Arial, sans-serif;
}

.info-container, .map-container {
    margin-top: 20px;
}

.map-container iframe {
    width: 100%;
    height: 400px;
}

/* Stili responsivi */
@media (min-width: 768px) and (max-width: 1024px) {
    .map-container iframe {
        height: 500px;
    }
}

@media (max-width: 767px) {
    .map-container iframe {
        height: 300px;
    }
}
